'use client'
import { toast } from 'sonner'
import { useEffect, useState } from 'react'
import { ethers, type JsonRpcSigner, type BrowserProvider } from 'ethers'

interface WalletState {
  account: string
  isConnected: boolean
  chainId: number | null
  signer: JsonRpcSigner | null
  provider: BrowserProvider | null
}

interface UseWalletReturn extends WalletState {
  disconnectWallet(): void
  connectWallet(): Promise<boolean>
}

export default function useWallet(): UseWalletReturn {
  const [account, setAccount] = useState('')
  const [isConnected, setIsConnected] = useState(false)
  const [chainId, setChainId] = useState<number | null>(null)
  const [signer, setSigner] = useState<JsonRpcSigner | null>(null)
  const [provider, setProvider] = useState<BrowserProvider | null>(null)

  //* --- è¿žæŽ¥é’±åŒ…å’Œåˆå§‹åŒ– provider å’Œ signer ---
  const connectWallet = async () => {
    if (typeof window.ethereum === 'undefined') {
      toast.error('è¯·å…ˆå®‰è£… MetaMask é’±åŒ…æˆ–å…¶ä»–ä»¥å¤ªåŠé’±åŒ…æ‰©å±•ï¼ŒåŽå†åˆ·æ–°é¡µé¢')
      return false
    }

    try {
      const accounts = await window.ethereum.request({
        method: 'eth_requestAccounts',
      })

      if (!accounts.length) {
        throw new Error('ç”¨æˆ·æ‹’ç»äº†è¿žæŽ¥è¯·æ±‚')
      }

      await initializeProviderAndSigner(accounts[0])
      setupEventListeners()
      return true
    } catch (err: any) {
      console.error('è¿žæŽ¥é’±åŒ…å¤±è´¥', err)
      toast.error(err.message)
      return false
    }
  }

  const disconnectWallet = () => {
    setAccount('')
    setChainId(null)
    setSigner(null)
    setProvider(null)
    setIsConnected(false)
    window.ethereum?.removeListener('accountsChanged', handleAccountsChanged)
    window.ethereum?.removeListener('chainChanged', handleChainChanged)
  }

  const initializeProviderAndSigner = async (accountAddress: string) => {
    if (!window.ethereum) return

    const web3Provider = new ethers.BrowserProvider(window.ethereum)
    const network = await web3Provider.getNetwork()
    const web3Signer = await web3Provider.getSigner()

    setIsConnected(true)
    setSigner(web3Signer)
    setProvider(web3Provider)
    setAccount(accountAddress)
    setChainId(Number(network.chainId))
  }

  //* --- å¤„ç†è´¦æˆ·å˜æ›´ ---
  //* è¿žæŽ¥è´¦æˆ·å˜æ›´
  const handleAccountsChanged = (accounts: string[]) => {
    if (!handleAccountsChanged.length) disconnectWallet()
    else {
      setAccount(accounts[0])
      initializeProviderAndSigner(accounts[0])
    }
  }

  //* è¿žæŽ¥ç½‘ç»œå˜æ›´
  const handleChainChanged = (chainIDHex: string) => {
    const newChanID = parseInt(chainIDHex, 16)
    setChainId(newChanID)
    console.log('è¿žæŽ¥ç½‘ç»œå˜æ›´', newChanID)
    toast.success(`å·²è¿žæŽ¥åˆ°ç½‘ç»œ: ${newChanID}`)
  }

  const setupEventListeners = () => {
    if (!window.ethereum) return
    window.ethereum.on('accountsChanged', handleAccountsChanged)
    window.ethereum.on('chainChanged', handleChainChanged)
  }

  //* --- æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€ ---
  const checkIsWalletConnected = async () => {
    if (typeof window.ethereum === 'undefined') {
      toast.error('è¯·å…ˆå®‰è£… MetaMask é’±åŒ…ï¼ŒåŽå†åˆ·æ–°é¡µé¢')
      return
    }

    try {
      const accounts: string[] = await window.ethereum.request({
        method: 'eth_accounts',
      })

      if (accounts.length > 0) {
        await initializeProviderAndSigner(accounts[0])
      }
    } catch (err) {
      console.error('æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€å¤±è´¥', err)
      toast.error('æ£€æŸ¥é’±åŒ…è¿žæŽ¥çŠ¶æ€å¤±è´¥')
    }
  }

  useEffect(() => {
    checkIsWalletConnected()
  }, [])

  return {
    signer,
    account,
    chainId,
    provider,
    isConnected,
    connectWallet,
    disconnectWallet,
  }
}
